import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
export default {
	data() {
		return {
			oldpassword:'',
			newpassword:'',
			newpassword2:'',
			rule:{
				rule:/^.{5,20}$/,
				msg:"密码长度必须为5-20个字符"
			},
		}
	},
	computed: {
		...mapState({
			userInfo:state=>state.user.userInfo,
		})
	},
	onLoad() {
		
	},
	methods: {
		submit(){
			if(!this.rule.rule.test(this.oldpassword) || !this.rule.rule.test(this.newpassword) || 
			 !this.rule.rule.test(this.newpassword2)){
				return uni.showToast({
					title:this.rule.msg,
					icon:'none'
				})
			}
			
			if(this.newpassword !== this.newpassword2){
				return uni.showToast({
					title:'两次密码不一致',
					icon:'none'
				})
			}
			
			this.$H.post('/user/user/changepass/'+this.userInfo.id,{
				old:this.oldpassword,
				new1:this.newpassword,
				new2:this.newpassword2
			},{
				token:true
			}).then(res=>{
				uni.showToast({
					title: res,
					icon:'none'
				});
				uni.navigateBack({
					delta:1
				})
			}).catch(err=>{
				
			})
			
		},
		update1(e){
			this.oldpassword = e.detail.value
		},
		update2(e){
			this.newpassword = e.detail.value
		},
		update3(e){
			this.newpassword2 = e.detail.value
		}
	}
}
